<template>
    <div class="block mb-5">
      <Input v-model="data.product_name" type="text" label="Item Name" class="w-full" :disabled="disabled" />
    </div>
    <div class="block mb-5">
        <Input v-model="data.imei_sn" type="text" label="IMEI/Serial Number" class="w-full" :disabled="disabled" />
    </div>
    <!-- <div class="block mb-5">
        <Input v-model="data.condition" type="text" label="Condition" class="w-full" :disabled="disabled" />
    </div> -->
    <label>Condition</label>
    <template v-if="isCustomCondition">
      <CustomConditionInput
      v-model="data.condition"
      placeholder="Input condition"
      class="mb-5"
      @confirm="handleConfirm"
      @cancel="() => {
        isCustomCondition = false
        data.condition = initialCondition
      }
      "
      :error="isEmptyConditionInput || isAlreadyExistCondition"
      />
      <small v-if="isEmptyConditionInput || isAlreadyExistCondition" class="text-red-base block -mt-4">{{ isEmptyConditionInput ? 'This field is required.' : 'This custom condition already exists.' }}</small>
    </template>
    <template v-else>
      <BSelect
      :model-value="{
        id: data.condition,
        condition_name: data.condition,
      }"
      @update:model-value="selectCondition"
      item-name="condition_name"
      by="condition_name"
      type="text"
      class="w-full mb-5"
      block
      placeholder="Select Condition"
      :items="
      conditionOptions?.conditions
      "
      >
        <template #append="{ collapse }">
          <template v-if="conditionOptions?.custom_conditions?.length">
            <label class="px-5 pt-4 pb-2">Saved Custom Condition</label>
            <div
              v-for="condition in conditionOptions.custom_conditions" :key="condition.id"
                      class="group flex items-center justify-between gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full">
                      <p @click="() => {
                        data.condition = condition.name
                        initialCondition = condition.name
                        collapse()
                      }" class="cursor-pointer w-full">
                      {{ condition.name }}
                    </p>
                    <Button
                    color="red"
                    dense
                    view="smooth"
                    class="group-hover:opacity-100 opacity-0"
                    :loading="deleting == condition.id"
                    @click="deleteCustomCondition(condition.id)"
                    >
                    <i class="material-icons">delete</i>
                  </Button>
                </div>
              </template>
              <button
              @click="() => {
                isCustomCondition = true
                data.condition = ''
              }"
                      class="flex items-center gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full"
                      >
                      <i class="material-icons">add</i> Add Custom Condition
                    </button>
                  </template>
                  <template #empty>
                    <span></span>
                  </template>
                </BSelect>
              </template>
                <div class="block mb-5">
        <Input v-model="data.price" type="number" label="Price" class="w-full" :disabled="disabled" :currency="true" />
    </div>
    <div class="block mb-5">
        <Input v-model="data.cost" type="number" label="Cost" class="w-full" :disabled="disabled" :currency="true" />
    </div>
</template>

<script>
import {defineComponent, reactive, ref, watch, onMounted, computed} from 'vue'
import { useCondition } from 'composable/invoice'
import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import BSelect from 'components/BSelect/BSelect.vue'
import Button from 'bbUi/components/button/button.vue'
import CustomConditionInput from 'components/CustomConditionInput.vue'

export default defineComponent({
  components: {
    Input,
    Checkbox,
    BSelect,
    Button,
    CustomConditionInput
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        product_name: null,
        is_mac: false,
        condition: null,
        price: null,
        imei_sn: null,
        cost: null
      })
    },
    // conditions: {
    //   type: Array,
    //   default: () => ([])
    // },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:data'
  ],
  setup(props, { emit }) {
    const { getConditions: actionGetConditions, actionDelete, actionCreate } = useCondition()
    const data = reactive({...props.data})
    const conditionOptions = ref({})
    const deleting = ref(null)
    const isCustomCondition = ref(false)
    const initialCondition = ref(null)

    const getConditions = async () => {
      const response = await actionGetConditions()
      conditionOptions.value = response
    }

    const isEmptyConditionInput = computed(() => {
      return data.condition.length === 0
    })

    const isAlreadyExistCondition = computed(() => {
      return conditionOptions.value.custom_conditions.some(item => item?.name?.toLowerCase() === data?.condition.toLowerCase())
    })

    const selectCondition = async (item) => {
      initialCondition.value = item.condition_name
      data.condition = item.condition_name
      data.condition_name = item.condition_name
    }

    const handleConfirm = async () => {
      await actionCreate({
        condition_name: data.condition,
        condition_term: "[]"
      })
      getConditions()
      initialCondition.value = data.condition
      isCustomCondition.value = false
    }

    const deleteCustomCondition = async (id) => {
      try {
        deleting.value = id
        await actionDelete(id)
        getConditions()
      } catch (error) {
        console.log(error)
      } finally {
        deleting.value = null
      }
    }

    watch(() => ({...data}), () => {
      Object.assign(data, {
        is_custom: 1,
        is_mac: 0 // Temporary CHANGE IT LATER
      })
      emit('update:data', data)
    })

    onMounted(() => {
      getConditions()
    })

    return {
      data,
      conditionOptions,
      selectCondition,
      getConditions,
      deleteCustomCondition,
      deleting,
      isCustomCondition,
      handleConfirm,
      initialCondition,
      isEmptyConditionInput,
      isAlreadyExistCondition
    }
  }
})
</script>
