<template>
  <div class="grid grid-cols-3 gap-4">
    <div class="xl:col-span-1 lg:col-span-1 col-span-3">
      <Tab v-model="data.payment_method" by="name" :items="items" component type="vertical" />
    </div>
    <div class="xl:col-span-2 lg:col-span-2 col-span-3">
      <RecordPaymentForm v-model:data="data" />
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, watch} from 'vue'
import Tab from 'bbUi/components/tab/tab.vue'
import RecordPaymentForm from './RecordPayment/Form.vue'

export default defineComponent({
  components: {
    Tab,
    RecordPaymentForm
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        amount: 0,
        maxAmount: null,
        note: null,
        payment_method: null
      })
    },
    items: {
      type: Array,
      default: () => ([])
    },
    maxAmount: {
      type: Number,
      default: 0
    }
  },
  emits: [
    'update:data'
  ],
  setup(props, { emit }) {
    const data = reactive(props.data)
    watch(() => ({...data}), () => {
      emit('update:data', data)
    })
    data.payment_method = props.items[0] ? props.items[0].name : null
    props.data.amount = props.maxAmount || 0
    props.data.maxAmount = props.maxAmount || null
    return {
      items: props.items,
      data
    }
  }
})
</script>