<template>
  <div>
    <div class="grid grid-cols-6 my-10">
      <div class="md:col-span-2 col-span-6 md:col-start-5 col-start-1">
        <div class="flex justify-between">
          <span class="font-bold">Subtotal</span>
          <span class="text-grey-base">
            <Currency :value="items.sub_total" />
          </span>
        </div>
        <div class="flex justify-between mt-2">
          <span class="font-bold">Total Cost</span>
          <span class="text-grey-base">
            <Currency :value="items.total_cost" />
          </span>
        </div>
        <div class="flex justify-between py-2 border-grey-01">
          <span class="font-bold">Profit</span>
          <span class="text-grey-base">
            <Currency :value="items.total_profit" /> <span v-if="items.profit_percentage != '0.0'">({{ items.profit_percentage }}%)</span>
          </span>
        </div>
        <div class="flex justify-between border-t border-b py-4 border-grey-01">
          <span class="font-bold">Total</span>
          <h6 class="font-bold">
            <Currency :value="items.total" />
          </h6>
        </div>
        <div class="status-history border-b border-grey-01 mb-8 mt-4 py-2">
          <template v-if="state.data.length != 0">
            <p class="font-bold">Record Payment</p>
            <div v-for="(history, index) in state.data" class="d-flex justify-content-between align-items-start my-4" :key="index">
              <div class="grid grid-cols-4">
                <div class="col-span-3">
                  <ExpandablePanel v-model="history.collapse">
                    <small class="block text-grey-base">by: {{ history.admin.name }}</small>
                    <small class="block text-grey-base">{{ history.created_at }}</small>
                    <small class="block text-grey-base">{{ history.note ? history.note : '-' }}</small>
                    <template #header="{ toggle }">
                      <p @click="toggle" class="flex items-center mb-0" :class="[history.return_id ? 'text-red-base' : 'text-dark']">
                        <span class="material-icons mr-2 text-2xl transform transition-all" :class="{ 'rotate-90': history.collapse }">chevron_right</span> {{ `Amount ${history.return_id ? 'Return' : 'Paid'} (${history.payment_method})` }}
                        <!-- <template v-if="history.payment_method == 'Paper Check' && history.return_id">
                          <Button view="smooth" class="ml-4" color="teal" :loading="loading.paperCheckInvoice" @click="printPaperCheck(history.id)" dense><i class="material-icons text-lg">print</i></Button>
                        </template> -->
                      </p>
                    </template>
                  </ExpandablePanel>
                </div>
                <div class="col-span-1">
                  <span class="p-4 block text-right" :class="[history.return_id ? 'text-danger' : 'text-dark']">{{ history.amount }}</span>
                </div>
              </div>
            </div>
          </template>
          <div v-else class="text-center mb-3">
            <span class="font-weight-bold text-danger">There's no payment history change.</span>
          </div>
        </div>
        <Card class="mb-6">
          <div class="d-flex justify-content-between align-items-center">
            <label class="font-weight-bold mb-0">Total Balance Due</label>
            <h3 class="font-weight-bold mb-0">
              <Currency :value="state.balance_due" />
            </h3>
          </div>
        </Card>
        <Button color="teal" :disabled="items?.items.length === 0 || disabled" @click="openModal('create')" block>RECORD PAYMENT</Button>
      </div>
    </div>
    <Modal v-model="modal.create" title="Record Payment" @close="unsetData">
      <RecordPayment v-model:data="data" :max-amount="state.balance_due" :items="statePaymentMethod" />
      <template #footer>
        <Button color="teal" :loading="loading.create" @click="createRecord">Save Payment</Button>
      </template>
    </Modal>
  </div>
</template>

<script>
import { computed, defineComponent } from 'vue'
import { useRoute } from 'vue-router'
import { useShowPaymentMethod, useShowRecord, usePrint, useShow, useShowItems } from 'composable/invoice'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Card from 'bbUi/components/card/card.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import RecordPayment from 'components/Invoice/RecordPayment.vue'
import Currency from 'components/Currency.vue'

export default defineComponent({
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
     items: {
      type: Object,
      default: ({})
    }
  },
  components: {
    Input,
    Button,
    Modal,
    Card,
    ExpandablePanel,
    RecordPayment,
    Currency
  },
  async setup(props) {
    const { data, state, loading, modal, openModal, unsetData, actionAll, actionCreate } = useShowRecord('invoice')
    const { state: statePaymentMethod } = useShowPaymentMethod('invoice')
    const { action: invoiceAction } = useShow('invoice')
    const { loading: loadingPrint, actionPaperCheckInvoice } = usePrint('invoice')
    // const { state: itemState } = useShowItems('invoice')
    const route = useRoute()

    await actionAll(route.params.buybackId)
    const createRecord = async () => {
      await actionCreate(route.params.buybackId)
      await invoiceAction(route.params.buybackId)
    }
    const printPaperCheck = async (recordId) => {
      await actionPaperCheckInvoice(route.params.buybackId, recordId)
    }
    const items = computed(() => {
      const items = props.items
      return items
    })

    return {
      data,
      state,
      statePaymentMethod,
      items,
      loading,
      loadingPrint,
      modal,
      unsetData,
      openModal,
      createRecord,
      printPaperCheck,
    }
  }
})
</script>