<template>
	<div>
    <Modal v-model="isShowModal" title="Change Conditions" @close="handleCloseModal" size="xl">
      <div class="flex mb-6">
        <Select class="mr-6" v-model="selectedCondition" :items="props.conditions" @change="selectCondition" name="condition_name" by="condition_name" label="Set All New Condition to" placeholder="leave it as is" :disabled="loading" />
        <Input v-model="priceAll" type="number" @keyup="setPriceAll" label="Set All Unit Price to" icon :disabled="loading">
          <template #icon>
            {{ me.currency_symbol }}
          </template>
        </Input>
      </div>
      <div class="max-h-96 overflow-y-auto">
        <BBTable :items="items" :selectable="false" :check-all="false" :header="['Item', 'IMEI', 'Condition', 'Unit Price', 'Profit', 'New Condition', 'New Unit Price', 'New Profit']" collapse-all>
          <template #item="{item}">
            <BBTColumn>
              <p class="font-semibold text-grey-04">{{item.product_name}}</p>
            </BBTColumn>
            <BBTColumn>
              <p class="text-grey-04">{{item.imei_sn}}</p>
            </BBTColumn>
            <BBTColumn>
              <p class="text-grey-04">{{item.condition}}</p>
            </BBTColumn>
            <BBTColumn>
              <p class="text-grey-04 text-right">${{money(item.price)}}</p>
            </BBTColumn>
            <BBTColumn>
              <p class="text-grey-04 text-right">${{money(item.price - item.cost)}} ({{ item.profit_percentage}}%)</p>
            </BBTColumn>
            <BBTColumn class="border-separator">
              <p class="text-grey-04">{{ item?.new_condition ? item?.new_condition : '-' }}</p>
            </BBTColumn>
            <BBTColumn>
              <Input v-model="item.new_price" type="text" @keyup="calculateNewProfitPercentage" @keypress="filterNumber" icon :currency="true" :disabled="loading">
                <template #icon>
                  {{ me.currency_symbol }}
                </template>
              </Input>
            </BBTColumn>
            <BBTColumn>
              <p class="text-grey-04 text-right">${{money(item?.new_price ? item?.new_price - item.cost : 0)}} ({{ `${item?.new_profit_percentage ? item.new_profit_percentage : 0}` }} %)</p>
            </BBTColumn>
          </template>
        </BBTable>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div>
            &nbsp;
          </div>
          <div>
            <Button view="smooth" class="mr-2" color="white" @click="handleCloseModal" :disabled="loading">Cancel</Button>
            <Button color="teal" @click="save" :loading="loading">Apply Changes</Button>
          </div>
        </div>
      </template>
    </Modal>
	</div>
</template>

<script>
import { defineComponent, ref, watch, computed } from 'vue'
import { useCondition } from 'composable/invoice'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import BBTable from 'components/Table/BBTable.vue'
import BBTColumn from 'components/Table/BBTColumn.vue'

import ItemList from 'components/Invoice/Condition/ItemList.vue'
import ChooseCondition from 'components/Invoice/Condition/ChooseCondition.vue'

import { money } from 'utils/number'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Input,
    Select,
    Button,
    Modal,
    Alert,
    ItemList,
    ChooseCondition,
    BBTable,
    BBTColumn
  },
  props: {
    items: {
      type: Array,
      default: () => ([])
    },
    conditions: {
      type: Array,
      default: () => ([])
    },
    show: {
      type: Boolean,
      default: () => false
    },
    loading: {
      type: Boolean,
      default: () => false
    },
    me: {
      type: Object,
      default: () => ([])
    }
  },
  emits: ['close', 'submit'],
  setup(props, { emit }) {
    const refComponent = ref(null)
    const isShowModal = ref(props.show)
    const currentStep = ref(1)
    const selectedIds = ref([])
    const selectedCondition = ref('')
    const priceAll = ref(null)

    const disableNext = computed(() => {
      if(!selectedIds.value.length) return true
      if(currentStep.value === 2 && !selectedCondition.value) return true
      return false
    })

    const componentBind = computed(() => {
      if(currentStep.value === 1) return {
          items: props.items || [],
          onSelectionChange: (selectedItems) => {
            selectedIds.value = selectedItems.map(item => item.id || null).filter(item => !!item)
          }
        }
      if(currentStep.value === 2) return {
        conditions: props.conditions || [],
        onSelectionChange: (condition) => selectedCondition.value = condition.condition_name || ''
      }
    })
    const componentName = computed(() => {
      if(currentStep.value === 1) return 'ItemList'
      if(currentStep.value === 2) return 'ChooseCondition'
    })
    
    const resetState = () => {
      currentStep.value = 1
      selectedIds.value = []
      selectedCondition.value = ''
    }

    const handleCloseModal = () => {
      resetState()
      isShowModal.value = false
      emit('close', true)
    }
    const handlePreviousStep = () => currentStep.value > 1 && --currentStep.value
    const handleNextStep = () => {
      if(currentStep.value === 2){
        emit('submit', {
          ids: JSON.stringify(selectedIds.value),
          condition: selectedCondition.value
        })
      }
      if(currentStep.value < 2 ) ++currentStep.value
    }

    watch(() => props.show, (isShow) => {
      isShowModal.value = isShow
    })

    const filterNumber = ($event) => {
      const chars = [...$event.target.value]
      const keyCode = ($event.keyCode ? $event.keyCode : $event.which)

      if (keyCode < 48 || keyCode > 57) {
        if (keyCode != 46) {
          $event.preventDefault()
        }

        if (keyCode == 46) {
          chars.map(char => {
            if (char === '.') {
              $event.preventDefault()
            }
          })
        }
      }
    }

    const save = () => {
      const payload = {
        data: props.items.map((item) => {
          return {
            id: item.id,
            condition: item?.new_condition ? item?.new_condition : item?.condition,
            price: item?.new_price ? item?.new_price : item?.price,
          }
        })
      }

      emit('submit', payload)
      priceAll.value = null
      selectedCondition.value = 'leave it as is'
    }

    const setPriceAll = () => {
      if (priceAll.value != '') {
        for (let i = 0; i < props.items.length; i++) {
          props.items[i].new_price = priceAll.value;
        }

        calculateNewProfitPercentage()
      }
    }

    const calculateNewProfitPercentage = () => {
      for (let i = 0; i < props.items.length; i++) {
        let new_profit_percentage = 0
        if (props.items[i].new_price) {
          new_profit_percentage = Math.ceil((((props.items[i].new_price - props.items[i].cost) / props.items[i].new_price * 100)).toFixed(2));
        }
        props.items[i].new_profit_percentage = new_profit_percentage;
      }
    }

    const selectCondition = () => {
      for (let i = 0; i < props.items.length; i++) {
        props.items[i].new_condition = selectedCondition.value;
      }
    }


    return {
      props,
      refComponent,
      currentStep,
      isShowModal,
      componentName,
      componentBind,
      selectedIds,
      selectedCondition,
      disableNext,
      handleCloseModal,
      handleNextStep,
      handlePreviousStep,
      save,
      money,
      filterNumber,
      priceAll,
      setPriceAll,
      calculateNewProfitPercentage,
      selectCondition
    }
  }
})
</script>

<style lang="scss" scoped>
  .border-separator {
    border-left: 2px solid #c5c5c5;
  }
</style>
