<template>
  <Card class="mb-6">
    <template #header>
      <div class="flex items-center justify-between">
        <h6>Items</h6>
        <div>
          <Button v-if="isCompletedInvoice" color="orange" dense link :to="{name: 'invoiceDetailReturn'}" class="mr-4"><span class="material-icons text-lg">replay</span> Return Item</Button>
          <Button v-if="!isCompletedInvoice" color="teal" @click="openModal('create')" dense><span class="material-icons">add</span> Add Item</Button>
        </div>
      </div>
    </template>
    <template #default>
      <div  class="mb-4">
        <Button v-if="!!selectedItems.length" @click="handleBulkChangeCondition" color="teal" dense>Change Conditions and Payout</Button>
        <Button v-if="!!selectedItems.length" @click="openModalBulkDelete('bulkDelete')" class="ml-2" color="red" dense>Delete Selected</Button>
      </div>
      <BBTable ref="refItemTable" :items="items.items" :loading="loading.all" children="child" :selectable="true" :header="[{ label: 'Item #', sortable: true, sortby: 'item', sortActive: itemSort }, 'Item', 'Condition', { label: 'Qty', align: 'right' }, { label: 'Unit Price', align: 'right', sortable: true, sortby: 'price', sortActive: itemSort }, { label: 'Total', align: 'right' }, '']" :disabled="isCompletedInvoice" @selection-change="handleItemSelect" @sort="handleItemSort"  collapse-all>
        <template #item="{item, collapsed}">
          <BBTColumn width="100">
            <span v-if="!item.child.length">{{item.id}}</span>
          </BBTColumn>
          <BBTColumn>
            <div v-if="!collapsed" class="flex">
              <p class="font-bold">{{ item.product_name }}</p>
              <Badge class="ml-2" color="red" v-if="item.return_id">Returned</Badge>
            </div>
            <template v-if="!item.child || (item.child && item.child.length === 0) && !collapsed">
              <small class="block mt-2">
                <template v-if="item.imei_sn !== null">({{ item.imei_sn }})</template>
                <span v-else class="text-red-base font-bold">IMEI/Serial Number not found</span>
              </small>
              <template v-if="item.detail">
                <small class="block">{{ item.detail }}</small>
                <br>
              </template>
              <div class="block mt-4">
                <Input v-model="item.notes" v-loading="loading.update[item.id]" :disabled="isCompletedInvoice" type="textarea" placeholder="Notes..." class="w-full" @focus-out="deviceTableUpdate(item)" dense />
              </div>
            </template>
          </BBTColumn>
          <BBTColumn>
            <template v-if="!item.child.length">
              <template v-if="isItemCustomCondition(item.id)">
                <CustomConditionInput
                  v-model="item.condition"
                  :disabled="isCompletedInvoice || creating"
                  placeholder="Input condition"
                  :error="!item.condition ||
                  customConditionOptions?.some(condition => condition.condition_name.toLowerCase() === item.condition.toLowerCase()) ||
                  getConditionItems(item)?.some(condition => condition.condition_name.toLowerCase() === item.condition.toLowerCase())
                  "
                  @confirm="(condition) => confirmCondition(condition, item.id)"
                  @cancel="() => setItemCustomCondition(item.id, false)"
                />
                <small
                class="text-red-base"
                v-if="!item.condition ||
                customConditionOptions?.some(condition => condition.condition_name.toLowerCase() === item.condition.toLowerCase()) ||
                getConditionItems(item)?.some(condition => condition.condition_name.toLowerCase() === item.condition.toLowerCase())
                ">{{ !item.condition ? 'This field is required.' : 'This custom condition already exists.' }}</small>
              </template>
              <template v-else>
                <BSelect
                  :model-value="{
                   id: item.condition,
                   condition_name: item.condition,
                  }"
                  @update:model-value="(condition) => handleChangeItemCondition(item, condition)"
                  v-loading="loadingCondition.update[item.id]"
                  item-name="condition_name"
                  by="condition_name"
                  type="text"
                  class="w-full"
                  block
                  placeholder="Select Condition"
                  :items="
                    getConditionItems(item)
                  "
                  :disabled="isCompletedInvoice">
                  <template #prepend>
                    <label class="p-4">{{ 'Active Condition' }}</label>
                  </template>
                  <template #append="{ collapse }">
                    <template v-if="customConditionOptions.length">
                      <label class="px-5 pt-4 pb-2">Saved Custom Condition</label>
                      <div
                      v-for="condition in customConditionOptions" :key="condition.id"
                      class="group flex items-center justify-between gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full">
                          <p @click="() => {
                          item.condition = condition.condition_name
                          handleChangeItemCondition(item, condition)
                          collapse()
                        }" class="cursor-pointer w-full">
                      {{ condition.condition_name }}
                      </p>
                        <Button
                          @click="deleteCustomCondition(condition.id)"  
                          :loading="deleting == condition.id"
                          color="red"
                          dense
                          view="smooth"
                          class="group-hover:opacity-100 opacity-0"
                        >
                          <i class="material-icons">delete</i>
                        </Button>
                      </div>
                    </template>
                    <button
                      @click="() => {
                        setItemCustomCondition(item.id, true)
                      }"
                      class="flex items-center gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full"
                    >
                      <i class="material-icons">add</i> Add Custom Condition
                    </button>
                  </template>
                  <template #empty>
                    <div class="p-4 text-center w-72">This item does not have an active condition, because it is a custom item.</div>
                  </template>
                </BSelect>
              </template>
            </template>
          </BBTColumn>
          <BBTColumn align="right">
            {{!collapsed ? item.qty : ''}}
          </BBTColumn>
          <BBTColumn>
            <template v-if="!item.child || (item.child && item.child.length === 0)">
              <Input v-model="item.price" v-loading="loading.update[item.id]" type="number" :disabled="isCompletedInvoice" :value="item.price" :currency="true" @focus-out="deviceTableUpdate(item)" block dense icon>
                <template #icon>
                  {{ me.currency_symbol }}
                </template>
              </Input>
              <div class="block text-right text-sm my-2">Cost: <b><Currency :value="item.cost" /></b></div>
              <div class="block text-right text-sm my-2">Profit: <b><Currency :value="item.price - item.cost" /> <span v-if="item.cost > 0">({{ item.profit_percentage }}%)</span></b></div>              
            </template>
          </BBTColumn>
          <BBTColumn align="right">
            <Currency v-if="!collapsed" :value="item.total" />
          </BBTColumn>
          <BBTColumn>
            <template v-if="!item.child || item.child.length === 0">
              <Button color="red" :disabled="isCompletedInvoice" @click="openModalSelected('delete', item)" dense><span class="material-icons text-sm">delete</span></Button>
            </template>
          </BBTColumn>
        </template>
        <template #child="{child}">
          <BBTColumn>
            <span>{{child.id}}</span>
          </BBTColumn>
          <BBTColumn>
            <p class="font-bold">{{ child.product_name }}</p>
            <small v-if="child.imei_sn" class="block mt-2">
              ({{ child.imei_sn || '-' }})
            </small>
            <small class="block">{{ child.detail || '-' }}</small>
            <br>
            <div class="block mt-4">
              <Input v-model="child.notes" v-loading="loading.update[child.id]" :disabled="isCompletedInvoice" type="textarea" placeholder="Notes..." class="w-full" @focus-out="deviceTableUpdate(child)" dense />
            </div>
          </BBTColumn>
          <BBTColumn>
            <template v-if="isItemCustomCondition(child.id)">
              <CustomConditionInput
                v-model="child.condition"
                :disabled="isCompletedInvoice || creating"
                placeholder="Input condition"
                :error="!child.condition || customConditionOptions?.some(condition => condition.condition_name.toLowerCase() === child.condition.toLowerCase())"
                @confirm="(condition) => confirmCondition(condition, child.id)"
                @cancel="() => setItemCustomCondition(child.id, false)"
              />
              <small class="text-red-base" v-if="!child.condition || customConditionOptions?.some(condition => condition.condition_name.toLowerCase() === child.condition.toLowerCase())">{{ !child.condition ? 'This field is required.' : 'This custom condition already exists.' }}</small>
            </template>
            <template v-else>
              <BSelect
                :model-value="{
                  id: child.condition,
                  condition_name: child.condition,
                }"
                :items="
                  getConditionItems(child)
                "
                item-name="condition_name"
                by="condition_name"
                @update:model-value="(condition) => handleChangeItemCondition(child, condition)"
                :loading="loadingCondition.update[child.id]"
                :disabled="isCompletedInvoice"
                class="w-full"
                block
              >
                <template #prepend>
                  <label class="p-4">Active Condition</label>
                </template>
                <template #append="{ collapse } ">
                  <template v-if="customConditionOptions.length">
                    <label class="px-5 pt-4 pb-2">Saved Custom Condition</label>
                    <div
                    v-for="condition in customConditionOptions" :key="condition.id"
                    class="group flex items-center justify-between gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full">
                        <p @click="() => {
                        child.condition = condition.condition_name
                        handleChangeItemCondition(child, condition)
                        collapse()
                      }" class="cursor-pointer w-full">
                    {{ condition.condition_name }}
                    </p>
                      <Button
                        @click="deleteCustomCondition(condition.id)"  
                        :loading="deleting == condition.id"
                        color="red"
                        dense
                        view="smooth"
                        class="group-hover:opacity-100 opacity-0"
                      >
                        <i class="material-icons">delete</i>
                      </Button>
                    </div>
                  </template>
                    <button
                    @click="() => {
                      setItemCustomCondition(child.id, true)
                    }"
                    class="flex items-center gap-2 pl-6 border-t border-grey-01 p-4 hover:bg-grey-lighten w-full"
                    >
                    <i class="material-icons">add</i> Add Custom Condition
                  </button>
                </template>
                <template #empty>
                  <div class="p-4 text-center w-72">This item does not have an active condition, because it is a custom item.</div>
                </template>
              </BSelect>
            </template>
          </BBTColumn>
          <BBTColumn align="right">
            {{child.qty}}
          </BBTColumn>
          <BBTColumn>
            <Input v-model="child.price" v-loading="loading.update[child.id]" type="number" :disabled="isCompletedInvoice" :value="child.price" :currency="true" @focus-out="deviceTableUpdate(child)" block dense icon>
              <template #icon>
                {{ me.currency_symbol }}
              </template>
            </Input>
            <div class="block text-right text-sm my-2">Cost: <b><Currency :value="child.cost" /></b></div>
            <div class="block text-right text-sm my-2">Profit: <b><Currency :value="child.price - child.cost" /> <span v-if="child.cost > 0">({{ child.profit_percentage }}%)</span></b></div>  
          </BBTColumn>
          <BBTColumn align="right">
            <Currency :value="child.total" />
          </BBTColumn>
          <BBTColumn>
            <Button color="red" :disabled="isCompletedInvoice" @click="openModalSelected('delete', child)" dense><span class="material-icons text-sm">delete</span></Button>
          </BBTColumn>
        </template>
      </BBTable>
      <Suspense>
        <Record v-loading="loading.all" :disabled="isCompletedInvoice" :items="items" />
      </Suspense>
    </template>
  </Card>
  <Modal v-model="modal.delete" title="Delete Item" @close="unsetData">
    <p>Are you sure want to delete this item?</p>
    <template #footer>
      <Button color="teal" :loading="loading.delete" @click="deleteDevice">Delete Item</Button>
    </template>
  </Modal>
  <Modal size="xs" v-model="modal.bulkDelete" title="Delete Multiple Item" hide-header hide-footer @close="unsetData">
    <div class="text-center py-10">
      <img class="mb-10 mx-auto" src="@/assets/icon/attention.svg" alt="Attention Icon">
      <div class="mt-8 mb-10">
        <h5 class="mb-4">Deleting {{ selectedItems.length }} item</h5>
        <p>Are you sure want to delete {{ selectedItems.length }} item ?</p>
      </div>
      <Button view="outlined" color="white" class="mr-5" :disabled="loading.bulkDelete" @click="modal.bulkDelete = false">Cancel</Button>
      <Button color="red" :loading="loading.bulkDelete" @click="deleteBulkDevice">Yes, Delete</Button>
    </div>
  </Modal>
  <Modal v-model="modal.create" title="Add Item" size="md" @close="unsetData">
    <div v-loading="loading.create">
      <Device ref="refDeviceForm" v-model:data="data.device" :selected="data.device.selected" @choose="addDeviceChoose" />
    </div>
    <template #footer>
      <Button color="teal" :loading="loading.create" @click="addDevice" v-if="refDeviceForm && refDeviceForm.tab.selected !== 'InvoiceDeviceSearch'">Add Item</Button>
    </template>
  </Modal>
  <BulkCondition :loading="loadingCondition.bulk" :show="modalCondition.update" @close="handleCloseModalBulkCondition" :items="selectedItems" :conditions="stateConditions" :me="me" @submit="handleBulkChangeItemCondition" />
</template>

<script>
import { defineComponent, ref, computed, watch, reactive, onUnmounted } from 'vue'
import { useRoute } from 'vue-router'
import { useUser } from 'composable/user'
import { useShowItems, useShowRecord, useShow, useCondition } from 'composable/invoice'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Card from 'bbUi/components/card/card.vue'
import Select from 'bbUi/components/select/select.vue'
import BBTable from 'components/Table/BBTable.vue'
import BBTColumn from 'components/Table/BBTColumn.vue'
import Record from './Record.vue'
import Device from 'components/Invoice/Device.vue'
import BulkCondition from 'components/Invoice/Condition/BulkCondition.vue'
import { ElMessage } from 'element-plus'
import Badge from 'bbUi/components/badge/badge.vue'
import Currency from 'components/Currency.vue'
import { deepClone } from 'utils/deep-clone'
import debounce from 'utils/debounce'
import BSelect from 'components/BSelect/BSelect.vue'
import CustomConditionInput from 'components/CustomConditionInput.vue'

export default defineComponent({
  components: {
    Input,
    Button,
    Modal,
    Record,
    Card,
    Device,
    Badge,
    BBTable,
    BBTColumn,
    Select,
    Currency,
    BulkCondition,
    BSelect,
    CustomConditionInput
  },
  async setup() {
    const { data, sort: itemSort, state, loading, modal, selected, unsetData, openModal, actionAll, actionCreate, actionUpdate, actionDelete, actionBulkDelete } = useShowItems('invoice')
    const { state: stateConditions, stateItemConditionOptions, loading: loadingCondition, modal: modalCondition, actionAll: actionCondition, actionUpdate: actionUpdateCondition, actionBulkUpdate: actionBulkUpdateCondition, actionCreate: actionCreateCondition, getConditions: getConditionOptions, actionDelete: actionDeleteCondition } = useCondition()
    const { me } = useUser()
    const { actionAll: recordActionAll } = useShowRecord('invoice')
    const { state: invoiceState } = useShow('invoice')
    const route = useRoute()
    const refDeviceForm = ref(null)
    const refItemTable = ref(null)
    const deleting = ref(null)
    const creating = ref(false)
    const selectedItems = ref([])
    const items = reactive({})
    const itemSorts = reactive([
      { value: 'lowest_unit_price', name: 'Lowest Unit Price' },
      { value: 'higest_unit_price', name: 'Higest Unit Price' },
    ])

    itemSort.sort_by = window.localStorage.getItem('invoice-detail-item-sort_by') ? window.localStorage.getItem('invoice-detail-item-sort_by') : ''
    itemSort.order_by = window.localStorage.getItem('invoice-detail-item-order_by') ? window.localStorage.getItem('invoice-detail-item-order_by') : ''

    const selectedItemSort = ref('Sort By')
    const loadingItemSort = ref(false)

    const customConditionOptions = ref([]);
        
    const customConditionItems = ref({})

    const originalConditions = ref({});

    const setItemCustomCondition = (itemId, value) => {
      const itemToUpdate = findItemById(itemId);
      
      if (itemToUpdate) {
        if (value === true) {
          originalConditions.value[itemId] = itemToUpdate.condition;

          itemToUpdate.condition = '';

          if (itemToUpdate.child && itemToUpdate.child.length > 0) {
            itemToUpdate.child.forEach((child, index) => {
              const childKey = `${itemId}_child_${index}`;
              
              originalConditions.value[childKey] = child.condition;
              
              child.condition = '';
            });
          }
        } else if (value === false) {
          if (originalConditions.value[itemId]) {
            itemToUpdate.condition = originalConditions.value[itemId];
            delete originalConditions.value[itemId];
          }

          if (itemToUpdate.child && itemToUpdate.child.length > 0) {
            itemToUpdate.child.forEach((child, index) => {
              const childKey = `${itemId}_child_${index}`;
              
              if (originalConditions.value[childKey]) {
                child.condition = originalConditions.value[childKey];
                delete originalConditions.value[childKey];
              }
            });
          }
        }
      }

      customConditionItems.value = {
        ...customConditionItems.value,
        [itemId]: value
      }
    }

    const isItemCustomCondition = (itemId) => {
      return !!customConditionItems.value[itemId]
    }

    if(route.query.new) modal.create = true

    const rowKey = row => {
      const child = row.child ? '_parent' : '_child'
      return row.id + child
    }
    const isParent = (row, index) => {
      return !(row.child && row.child.length > 0)
    }
    const deviceTableUpdate = async (data) => {
      await actionUpdate(route.params.buybackId, data.id, {
        price: data.price || 0,
        notes: data.notes || ''
      }, (responseData) => {
        const invoiceResponse = responseData.data

        if(!!invoiceResponse && !!invoiceResponse?.items){
          // Update current data
          const editedItem = invoiceResponse.items

          const mappedItems = items.items.map((item, itemIndex) => {
            const hasChild = item.child.length > 0
            if(hasChild){
              const childs = item.child
              for(let [childIndex, child] of childs.entries()){
                if(child.id === data.id){
                  const newData = editedItem.child.find(item => item.id === data.id)
                  if(newData) item.child[childIndex] = newData
                  // update total on parent data
                  item.total = editedItem.total
                }
              }
              return item
            }
            if(item.id === data.id) item = editedItem
            return item
          })

          items.items = mappedItems

          delete invoiceResponse.items
          // Update invoice detail
          Object.assign(items, {...invoiceResponse})
        }
      })
      await recordActionAll(route.params.buybackId)
    }
    const addDeviceChoose = async (row) => {
      await actionCreate(route.params.buybackId, {
        ids: JSON.stringify([row.id])
      })
    }
    const addDevice = async() => {
      try {
        const deviceData = {...data.device}
        const isEmptyDevice = Object.keys(deviceData).length === 0
        if( isEmptyDevice ){
          ElMessage({
            message: 'Please fill the form below',
            type: 'warning'
          })
          return
        }
        const isBulkImei = Array.isArray(deviceData?.imei_sn)
        if( isBulkImei ) deviceData.imei_sn = JSON.stringify(deviceData.imei_sn)
        const isSelectedDevice = Array.isArray(deviceData?.selected) // device from table
        if( isSelectedDevice ){
          Object.assign(deviceData, {
            ids: JSON.stringify(deviceData.selected.map(device => device.id))
          })
          delete deviceData.selected
        }
        await actionCreate(route.params.buybackId, deviceData)
        await recordActionAll(route.params.buybackId)
      } catch (error) {
        console.log(error)
      }
    }
    const deleteDevice = async (id, data) => {
      await actionDelete(route.params.buybackId)
      await recordActionAll(route.params.buybackId)
    }
    const deleteBulkDevice = async () => {
      await actionBulkDelete({
        buybackId: route.params.buybackId,
        data: {
          ids: selectedItems.value.map((item) => item.id)
        }
      })
      await recordActionAll(route.params.buybackId)
      selectedItems.value = []
      refItemTable.value.checkValue = []
    }
    const openModalSelected = (target, item) => openModal(target, () => {
      selected.value = item.id
    })
    const openModalBulkDelete = (target) => openModal(target)

    const isCompletedInvoice = computed(() => invoiceState.value?.status_id === 3)

    const filterNumber = ($event) => {
      const chars = [...$event.target.value]
      const keyCode = ($event.keyCode ? $event.keyCode : $event.which)

      if (keyCode < 48 || keyCode > 57) {
        if (keyCode != 46) {
          $event.preventDefault()
        }

        if (keyCode == 46) {
          chars.map(char => {
            if (char === '.') {
              $event.preventDefault()
            }
          })
        }
      }
    }

    const invoiceDetailRef = ref(null)

    const expandIndent = (row) => {
      invoiceDetailRef.value.toggleRowExpansion(row)
    }

    const handleItemSelect = (items) => {
      selectedItems.value = items
    }

    const handleBulkChangeCondition = () => {
      if(!!selectedItems.value.length){
        modalCondition.update = true
      }
    }

    const handleChangeItemCondition = async (item, condition) => {
      try {
        const isValid = Object.keys(item).includes('id') && Object.keys(condition).includes('condition_name')
        if(isValid) {
          await actionUpdateCondition(route.params.buybackId, {
            id: item.id,
            condition: condition.condition_name
          }, () => {
            item.condition = condition.condition_name
            ElMessage({
              type: 'success',
              message: 'Item condition changed'
            })
          })
        }
      } catch (error) {
        console.log(error)
      }
    }

    const confirmCondition = async (condition, itemId) => {
      try {
        creating.value = true
        await actionCreateCondition({
          condition_name: condition,
          condition_term: "[]"
        }, async () => {
          const itemToUpdate = findItemById(itemId);
          
          if (itemToUpdate) {
            await actionUpdateCondition(route.params.buybackId, {
              id: itemId,
              condition: condition
            }, () => {
              customConditionItems.value = {
                ...customConditionItems.value,
                [itemId]: false
              };
              
              if (originalConditions.value[itemId]) {
                delete originalConditions.value[itemId];
              }
              
              if (itemToUpdate.child && itemToUpdate.child.length > 0) {
                itemToUpdate.child.forEach((child, index) => {
                  const childKey = `${itemId}_child_${index}`;
                  if (originalConditions.value[childKey]) {
                    delete originalConditions.value[childKey];
                  }
                });
              }
              
              loadConditionsForItem(itemToUpdate);
              
              ElMessage({
                type: 'success',
                message: 'Custom condition added and applied'
              });
            });
          } else {
            customConditionItems.value = {
              ...customConditionItems.value,
              [itemId]: false
            };
            
            if (originalConditions.value[itemId]) {
              delete originalConditions.value[itemId];
            }
            
            ElMessage({
              type: 'success',
              message: 'Custom condition added'
            });
          }
        });
      } catch (error) {
        console.log(error);
        ElMessage({
          type: 'error',
          message: 'Failed to add custom condition'
        });
      } finally {
        creating.value = false;
      }
    };

    const findItemById = (itemId) => {
      if (!items.items) return null;
      
      const mainItem = items.items.find(item => item.id === itemId);
      if (mainItem) return mainItem;
      
      for (const item of items.items) {
        if (item.child && item.child.length > 0) {
          const childItem = item.child.find(child => child.id === itemId);
          if (childItem) return childItem;
        }
      }
      
      return null;
    };

    const handleBulkChangeItemCondition = async (data) => {
      await actionBulkUpdateCondition(route.params.buybackId, data, async () => {
        await Promise.all([actionAll(route.params.buybackId), await recordActionAll(route.params.buybackId)])
        ElMessage({
          type: 'success',
          message: 'Item conditions updated'
        })
        modalCondition.update = false
        selectedItems.value = []
        refItemTable.value.checkValue = []
      })
    }

    const handleCloseModalBulkCondition = () => {
      modalCondition.update = false
    }

    const getConditions = (itemCondition) =>  {
      const isExistCondition = !!stateConditions.value.find(condition => condition.condition_name === itemCondition) || !itemCondition
      if(isExistCondition) return stateConditions.value
      stateConditions.value.push({condition_name: itemCondition})
      return stateConditions.value
    }

    const unWatchItems = watch(() => state.value, (newData) => {
      if(!!newData) Object.assign(items, deepClone(newData))
    })

    // onUnmounted(() => {
    //   unWatchItems()
    // })

    const sortDefault = ( b, a ) => {
      if ( a.id < b.id ) return -1;
      if ( a.id > b.id ) return 1;
      return 0;
    }
    
    const sortPriceASC = ( a, b ) => {
      if ( a.price < b.price ) return -1;
      if ( a.price > b.price ) return 1;
      return 0;
    }

    const sortPriceDESC = ( b, a ) => {
      if ( a.price < b.price ) return -1;
      if ( a.price > b.price ) return 1;
      return 0;
    }

    const sortPrice = debounce(() => {
      if (selectedItemSort.value == 'lowest_unit_price') {
        items.items = items.items.sort( sortPriceASC );
      }
      else if (selectedItemSort.value == 'higest_unit_price') {
        items.items = items.items.sort( sortPriceDESC );
      }
      else {
        items.items = items.items.sort( sortDefault );
      }

      loadingItemSort.value = false;
    }, 300)

    const changeItemSort = () => {
      loadingItemSort.value = true;
      sortPrice()
    }

    const handleItemSort = async (dataSort) => {
      window.localStorage.setItem('invoice-detail-item-sort_by', dataSort.sortby)
      window.localStorage.setItem('invoice-detail-item-order_by', dataSort.sortType)

      itemSort.sort_by = window.localStorage.getItem('invoice-detail-item-sort_by') ? window.localStorage.getItem('invoice-detail-item-sort_by') : ''
      itemSort.order_by = window.localStorage.getItem('invoice-detail-item-order_by') ? window.localStorage.getItem('invoice-detail-item-order_by') : ''
      
      await actionAll(route.params.buybackId)
    }

    const conditionsCache = ref({});
    const loadingStates = ref({});

    const loadConditionsForItem = async (item) => {
      const cacheKey = item?.category_id ? `category_${item.category_id}` : 
                      item?.product_id ? `product_${item.product_id}` : 
                      `condition_${item.condition}`;
      
      loadingStates.value[cacheKey] = true;
      
      try {
        let response;
        if (item?.category_id) {
          response = await getConditionOptions({
            category_id: item.category_id
          });
        } else if (item?.product_id) {
          response = await getConditionOptions({
            product_id: item.product_id
          });
        } else {
          const fallbackConditions = getConditions(item.condition);
          conditionsCache.value[cacheKey] = fallbackConditions;
          return fallbackConditions;
        }

        const formattedConditions = response.conditions.map(condition => ({
          id: condition.id,
          condition_name: condition.name
        }));

        const formattedCustomConditions = response.custom_conditions.map(condition => ({
          id: condition.id,
          condition_name: condition.name
        }));
        
        conditionsCache.value[cacheKey] = formattedConditions || [];
        customConditionOptions.value = formattedCustomConditions || [];
        
        return conditionsCache.value[cacheKey];
      } catch (error) {
        console.error('Error loading conditions:', error);
        const fallbackConditions = getConditions(item.condition);
        conditionsCache.value[cacheKey] = fallbackConditions;
        return fallbackConditions;
      } finally {
        loadingStates.value[cacheKey] = false;
      }
    };

    const getConditionItems = (item) => {
      const cacheKey = item?.category_id ? `category_${item.category_id}` : 
                      item?.product_id ? `product_${item.product_id}` : 
                      `condition_${item.condition}`;
      
      if (conditionsCache.value[cacheKey]) {
        return conditionsCache.value[cacheKey];
      }
      
      if (loadingStates.value[cacheKey]) {
        return getConditions(item.condition);
      }
      
      loadConditionsForItem(item).catch(error => {
        console.error('Failed to load conditions:', error);
      });
      
      return getConditions(item.condition);
    };

    const deleteCustomCondition = async (id) => {
     try {
       deleting.value = id
       await actionDeleteCondition(id)
       customConditionOptions.value = customConditionOptions.value.filter(condition => condition.id != id)
     } catch (error) {
       console.log(error)      
     } finally {
       deleting.value = null
      }
    }

    await Promise.all([
      await actionAll(route.params.buybackId),
    ])

    return {
      state,
      stateConditions,
      stateItemConditionOptions,
      items,
      me,
      data,
      refDeviceForm,
      refItemTable,
      selectedItems,
      isCompletedInvoice,
      loading,
      modal,
      loadingCondition,
      modalCondition,
      openModal,
      unsetData,
      rowKey,
      isParent,
      openModalSelected,
      openModalBulkDelete,
      deviceTableUpdate,
      addDevice,
      addDeviceChoose,
      deleteDevice,
      deleteBulkDevice,
      filterNumber,
      expandIndent,
      handleItemSelect,
      handleBulkChangeCondition,
      handleChangeItemCondition,
      handleCloseModalBulkCondition,
      handleBulkChangeItemCondition,
      getConditions,
      invoiceDetailRef,
      itemSorts,
      selectedItemSort,
      changeItemSort,
      loadingItemSort,
      handleItemSort,
      itemSort,
      isItemCustomCondition,
      setItemCustomCondition,
      confirmCondition,
      findItemById,
      getConditionItems,
      loadConditionsForItem,
      customConditionOptions,
      deleting,
      deleteCustomCondition,
      loadingStates,
      creating,
      originalConditions
    }
  }
})
</script>
