<template>
  <Tab v-model="tab.selected" :items="tab.items" component />
<!--  <component :is="tab.selected" :conditions="state" :data="data" @choose="" />-->
  <component v-bind="vBind" v-on="vOn" :is="tab.selected"  />
</template>

<script>
import { defineComponent, ref, computed, watch } from 'vue'
import { useShowItemsCreate } from 'composable/invoice'
// import { useAll } from 'composable/condition'
import Tab from 'bbUi/components/tab/tab.vue'
import InvoiceDeviceSearch from './Device/Search.vue'
import InvoiceDeviceBulk from './Device/Bulk.vue'
import InvoiceDeviceCustom from './Device/Custom.vue'

export default defineComponent({
  components: {
    Tab,
    InvoiceDeviceSearch,
    InvoiceDeviceBulk,
    InvoiceDeviceCustom
  },
  props: {
    data: [
      Array,
      Object
    ],
    selected: {
      Array
    }
  },
  emits: [
    'update:data',
    'choose'
  ],
  setup(props, { emit }) {
    const { tab } = useShowItemsCreate()
    // const { state, actionAll } = useAll()

    // await actionAll()
    const data = ref(null)
    const vOn = computed(() => {
      const on = {}
      on['update:data'] = e => data.value = e
      if(tab.selected === 'InvoiceDeviceSearch') on.choose = e => emit('choose', e)

      return on
    })
    const vBind = computed(() => {
      const bind = {}
      bind.data = data.value
      if(tab.selected === 'InvoiceDeviceSearch') bind.selected = props.selected

      return bind
    })
    watch(() => ({...data.value}), () => {
      emit('update:data', data.value)
    })
    watch(() => tab.selected, () => {
      data.value = {}
    })

    return {
      // state,
      data,
      tab,
      vOn,
      vBind
    }
  }
})
</script>