<template>
  <Input v-model="imei" type="textarea" label="Scan IMEI or Serial # into it's own row" class="w-full" :disabled="disabled" />
</template>

<script>
import { defineComponent, reactive, watch, computed } from 'vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  components: {
    Input
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        imei_sn: null
      })
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:data'
  ],
  setup(props, { emit }) {
    const data = reactive({...props.data})
    const imei = computed({
      get() {
        return data.imei_sn ? data.imei_sn.join('\n') : ''
      },
      set(value) {
        data.imei_sn = value.split('\n')
      }
    })

    watch(() => ({...data}), () => {
      emit('update:data', data)
    })

    return {
      imei
    }
  }
})
</script>
